body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.row{
  margin-bottom: 0px;
}

.shuffle{
  background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  padding: 20px;
  overflow-y: scroll;
  height: 100vh;
}

.shuffle>div{
  padding: 20px;
}

div>img{
  margin-bottom: 10px;
  cursor: pointer;
}

img:hover{
  box-shadow: 0 0 11px rgba(33,33,33,.2);
  border: 10px solid white;
}

.ordered{
  background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  overflow-y: scroll;
  height: 100vh;
}

.ordered>div{
  padding: 20px;
}

.infinity{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.infinity > span{
  padding: 0px 1rem 0px 1rem;
}

.space{
  color: #101D90;
  -webkit-animation: glow 1s infinite alternate;
          animation: glow 1s infinite alternate;
}

.mind{
  color:  #EDC005;
  -webkit-animation: glow 1.5s infinite alternate;
          animation: glow 1.5s infinite alternate;
}

.reality{
  color: #83000E;
  -webkit-animation: glow 2s infinite alternate;
          animation: glow 2s infinite alternate;
}

.power{
  color: #DD70FD;
  -webkit-animation: glow 1s infinite alternate;
          animation: glow 1s infinite alternate;
}

.time{
  color: #07A244;
  -webkit-animation: glow 1.5s infinite alternate;
          animation: glow 1.5s infinite alternate;
}

.soul{
  color: #CC5D18;
  -webkit-animation: glow 2s infinite alternate;
          animation: glow 2s infinite alternate;
}

@-webkit-keyframes glow{
  0%{
    opacity: 0.5;
  }
  50%{
    opacity: 1;
  }
  100%{
    opacity: 0.5;
  }
}

@keyframes glow{
  0%{
    opacity: 0.5;
  }
  50%{
    opacity: 1;
  }
  100%{
    opacity: 0.5;
  }
}
